import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useAdmissionReadmissionConfirmedList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import ConfirmedCard from "./ConfirmedCard";

const Confirmed = ({ aid, instituteId }) => {
  const { t } = useTranslation();

  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionReadmissionConfirmedList,
    admissionReadmissionConfirmedListLoading,
  } = useAdmissionReadmissionConfirmedList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionReadmissionConfirmedList?.list?.length) {
      let obj = nextPage(admissionReadmissionConfirmedList?.list?.length);
      setState(() => obj);
    }
  }, [admissionReadmissionConfirmedList?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !admissionReadmissionConfirmedListLoading &&
      admissionReadmissionConfirmedList?.list?.length >= 0
    ) {
      // if (e.target.value) {
      setSearchCopy(e.target.value);
      // } else {
      //   setSearchCopy("");
      //   setSearch("");
      // }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.7rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              name="search"
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
      </div>
      {admissionReadmissionConfirmedList?.list?.map((applicant, index) =>
        admissionReadmissionConfirmedList?.list?.length === index + 1 ? (
          <div key={applicant?._id} ref={ref}>
            <ConfirmedCard applicant={applicant} instituteId={instituteId} />
          </div>
        ) : (
          <ConfirmedCard
            applicant={applicant}
            key={applicant?._id}
            instituteId={instituteId}
          />
        )
      )}
      {admissionReadmissionConfirmedListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
        )
      ) : search ? (
        admissionReadmissionConfirmedList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No confirmed admission found related this search."
          />
        )
      ) : (
        admissionReadmissionConfirmedList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No confirmed admission list."
          />
        )
      )}
    </>
  );
};

export default Confirmed;
