import { useLocation, useParams, useSearchParams } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import FinanceAllStudent from "../../FinanceManager/FinanceAllStudent/FinanceAllStudent";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import Readmission from "./Readmission/Readmission";
import Confirmed from "./Confirmed/Confirmed";
const AdmissionReadmissionConfig = ({ instituteId, studentAlias, aid }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();

  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("re_admission_only")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/admission?type=internal${tab.urlKey}`
                : `/q/${params.username}/member/staff/admission?type=internal${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />

      {!searchParams.get("n") ? (
        <FinanceAllStudent
          instituteId={instituteId}
          studentAlias={studentAlias}
          invalidateAccess="unAuthorized"
          allStudentControl={allStudentControl}
        />
      ) : searchParams.get("n") === "re-admission" ? (
        <UserMemberContentWrapper>
          <Readmission aid={aid} instituteId={instituteId} />
        </UserMemberContentWrapper>
      ) : searchParams.get("n") === "confirmed" ? (
        <UserMemberContentWrapper>
          <Confirmed aid={aid} instituteId={instituteId} />
        </UserMemberContentWrapper>
      ) : null}
    </UserMemberContainerWrapper>
  );
};

export default AdmissionReadmissionConfig;

const tabList = [
  {
    label: "all_students",
    id: "student",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "internal_admission",
    id: "re-admission",
    urlKey: "&n=re-admission",
    groupIs: ["re-admission"],
  },
  {
    label: "confirm_admission",
    id: "confirmed",
    urlKey: "&n=confirmed",
    groupIs: ["confirmed"],
  },
];

const allStudentControl = {
  collectAccess: "Other",
  govtAccess: "Other",
  cancelAccess: false,
  editStructureControl: "unauthorized",
  editGovtNumberContorl: "unauthorized",
  editInstallmentControl: "unauthorized",
  editGovtInstallmentControl: "unauthorized",
};
